import * as api from '../api/'

export const fetchQuestion = url => {
	return dispatch => {
		api.getQuestion(url)
		.then(resp => {
			dispatch({
				type: 'question/store',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
}

export const postAnswer = (url, answerId, result) => {
	return dispatch => {
		api.postAnswer(url, answerId, result)
		.then(resp => {
			dispatch({
				type: 'question/store',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
}