import react, { useEffect, useCallback } from 'react'
import { Container, Card, Button, Form } from "react-bootstrap";
import { useSelector, useDispatch } from 'react-redux'
import { store, result } from './reducers/questionslice'
import { fetchQuestion, postAnswer } from './actions'
import { useParams } from 'react-router-dom'

import QEntry from './qentry'
import ThankYou from './thankyou'

export default function Question() {

	const params = useParams()	
	const l1title = useSelector((state) => state.question.l1title)
	const dispatch = useDispatch()
	
	useEffect(() => {
		dispatch(fetchQuestion(params.url))
	}, [])

	const title = l1title == null ? '' : l1title;

	return(
      <Container>
      	<div className="py-5 text-center">
		<img className="d-block mx-auto mb-4" src="/Sponsor_Logo.jpg" alt="" width="161" height="51" />
      	<h2>{title}</h2>
      	</div>
      	{ l1title != null && <QEntry /> }
      	{ l1title == null && <ThankYou /> }
      </Container>
	);
}